export default withRouter(Product);

export function createURLName(name) {
    const result = name.toLowerCase()
                        .normalize("NFD").replace(/[\u0300-\u036f]/g, "")
                        .replace(/-/g, "")
                        .replace(/"/g, "").replace(/'/g, "").replace(/\(|\)/g, "")
                        .replace(/ +/g, "-");

    return result;
}