import React from "react";

import Header from "../../../components/Header";

import Landing from "./views/Landing";
import Product from "./views/Product";

import "./styles/home.css";

import ChargerCarMountains1 from "../../../assets/images/charger-car-mountains-1.png";
import ChargerCarMountains2 from "../../../assets/images/charger-car-mountains-2.png";
import ChargerBikeMountains1 from "../../../assets/images/charger-bike-mountains-1.png";

export default class Home extends React.Component {

    componentDidMount() {
        var container = document.getElementById("home").childNodes[1];
        var scrollbar = document.getElementById("scrollbar");
        var scrollIndicator = document.getElementById("scrollbar").childNodes[0].childNodes[0];

        const contentHeight = window.innerHeight * 4;
        const scrollIndicatorHeight = parseInt(scrollbar.offsetHeight / (contentHeight / window.innerHeight));

        scrollIndicator.style.height = scrollIndicatorHeight + "px";

        container.addEventListener("scroll", () => {
           const value = (contentHeight - window.innerHeight) / container.scrollTop;
           const offset = parseInt((scrollbar.offsetHeight - scrollIndicatorHeight) / value);


           scrollIndicator.style.top = offset + "px";
        });
    }

    render() {
        return(
            <div id="home">
                <Header />

                <div className="container">
                    <Landing />
                    <Product name="Domáce nabíjačky" image={ChargerCarMountains1} />
                    <Product name="Business nabíjačky" image={ChargerCarMountains2} background />
                    <Product name="Nabíjačky pre elektrobicykle" image={ChargerBikeMountains1} />
                </div>

                <ScrollBar />
            </div>

        )
    }
}

function ScrollBar() {
    return(
        <div id="scrollbar">
            <div className="scrollbar-container">
                <div className="indicator" />
            </div>
        </div>
    )
}