import React from "react";

import Button from "../../../components/Button";

import "./styles/product.css";

import Charger from "../../../assets/images/charger-1.png";

export default class Product extends React.Component {

    state = {
        configurating: false
    }

    render() {
        return(
            <div id="product">
                <div className="image-panel">
                    <img className="image" src={Charger} />
                </div>

                <div className="info-panel" style={{ flex: this.state.configurating ? 2.5 : 1 }}>
                    <div className="panel">
                        <div className="name">{this.props.title}</div>

                        <br />
                        <br />

                        <Button
                            title="Konfigurovať"
                            onClick={() => this.setState((state) => ({ configurating: !state.configurating }))}
                            black
                        />
                    </div>
                </div>
            </div>

        )
    }
}