import React from "react";
import { BrowserRouter, HashRouter, Switch, Route } from "react-router-dom";

import "./styles/font.css";
import "./styles/animation.css";
import "./styles/app.css";

import Transition from "./components/Transition";
import Animation from "./components/Animation";

import Landing from "./screens/landing/Landing";

import Home from "./screens/products/home/Home";
import Product from "./screens/products/product/Product";

export default function Router() {
    return(
        <HashRouter basename="/">
            <Switch>
                <Route exact path="/"><Landing /></Route>

                <Route exact path="/produkty"><Home /></Route>

                <Route exact path="/produkty/domace-nabijacky"><Product title="Domáce nabíjačky" /></Route>
                <Route exact path="/produkty/business-nabijacky"><Product title="Business nabíjačky" /></Route>
                <Route exact path="/produkty/nabijacky-pre-elektrobicykle"><Product title="Nabíjačky pre elektrobicykle" /></Route>
            </Switch>

            <Transition />
            <Animation />
        </HashRouter>
    )
}