import React from "react";

export default class Animation extends React.Component {
    
    constructor() {
        super();

        this.handleScroll = this.handleScroll.bind(this);
        this.appendAnimationClass = this.appendAnimationClass.bind(this);
    }

    handleScroll() {
        var animatedComponents = document.getElementsByClassName("animated");

        for (let i = 0; i < animatedComponents.length; i++) {
            var component = animatedComponents[i];

            if (component.getBoundingClientRect().top <= window.innerHeight) {
                this.appendAnimationClass(component);
            }
        }
    }

    appendAnimationClass(component) {
        component.classList.add("animate__animated");

        if (component.classList.contains("fadeIn")) component.classList.add("animate__fadeIn");
        if (component.classList.contains("fadeInUp")) component.classList.add("animate__fadeInUp");
        if (component.classList.contains("fadeInDown")) component.classList.add("animate__fadeInDown");
        if (component.classList.contains("fadeInRight")) component.classList.add("animate__fadeInRight");
        if (component.classList.contains("fadeInLeft")) component.classList.add("animate__fadeInLeft");

        if (component.classList.contains("backInUp")) component.classList.add("animate__backInUp");

        if (component.classList.contains("zoomIn")) component.classList.add("animate__zoomIn");

        if (component.classList.contains("delay-1s")) component.classList.add("animate__delay-1s");
        if (component.classList.contains("delay-2s")) component.classList.add("animate__delay-2s");
        if (component.classList.contains("delay-3s")) component.classList.add("animate__delay-3s");
        if (component.classList.contains("delay-4s")) component.classList.add("animate__delay-4s");
        if (component.classList.contains("delay-5s")) component.classList.add("animate__delay-5s");
    }

    componentDidMount() {
        this.handleScroll();

        window.addEventListener("scroll", this.handleScroll);
    }
    
    componentWillUnmount() {
        window.removeEventListener("scroll", this.handleScroll);
    }

    render() {
        return null;
    }
}